import {
  IAuthenticationRestrictions,
  ITenantLoginOption,
  TenantLoginOptionType
} from '@c8y/client';

export interface TenantLoginOption
  extends Omit<
    ITenantLoginOption,
    | 'authenticationRestrictions'
    | 'self'
    | 'strengthValidity'
    | 'tfaStrategy'
    | 'greenMinLength'
    | 'enforceStrength'
    | 'strengthValidity'
    | '_type'
  > {
  authenticationRestrictions?: AuthenticationRestrictions;
}

export interface AuthenticationRestrictions
  extends Omit<IAuthenticationRestrictions, 'trustedUserAgents' | 'forbiddenUserAgents'> {
  trustedUserAgents: UserAgent[];
  forbiddenUserAgents: UserAgent[];
}

export class UserAgent {
  readonly _id: string;
  value: string;

  constructor(value: string) {
    this._id = this.uniqueId();
    this.value = value;
  }

  get id() {
    return this._id;
  }

  private uniqueId() {
    const dateString = Date.now().toString(36);
    const randomString = Math.random().toString(36).substr(2);
    return dateString + randomString;
  }
}

export function isOauthInternal(tenantLoginOption: TenantLoginOption) {
  return tenantLoginOption.type === TenantLoginOptionType.OAUTH2_INTERNAL;
}

export function isBasic(tenantLoginOption: TenantLoginOption) {
  return tenantLoginOption.type === TenantLoginOptionType.BASIC;
}
