import { Component, Input, SimpleChanges } from '@angular/core';
import { gettext } from '@c8y/ngx-components';
import { ControlContainer, NgForm } from '@angular/forms';
import { AuthConfiguration } from './auth-configuration.model';
import { TenantUiService } from '@c8y/ngx-components';
import { TenantLoginOptionType } from '@c8y/client';

@Component({
  selector: 'c8y-login-settings',
  templateUrl: './login-settings.component.html',
  viewProviders: [{ provide: ControlContainer, useExisting: NgForm }]
})
export class LoginSettingsComponent {
  PREFERRED_LOGIN_MODE_POPOVER = gettext(
    'Main difference is the storage of the authentication information. With Basic Auth, it is saved in a session storage and with OAI-Secure in a HttpOnly cookie. OAI-Secure grant is recommended as the authentication information is not accessible via JavaScript. Single sign-on redirect allows a user to login with a single 3rd-party authorization server using the OAuth2 protocol.'
  );
  ENFORCED_BY_PLATFORM_POPOVER = gettext('The setting is enforced on the platform level.');

  @Input()
  authConfiguration: AuthConfiguration;

  isOauth2: boolean;
  tenantLoginOptionTypeEnum = TenantLoginOptionType;

  private PASSWORD_CATEGORY = 'password';
  private LIMIT_VALIDITY_KEY = 'limit.validity';
  private TENANT_STRENGTH_VALIDITY_KEY = 'strength.validity';
  private SYSTEM_STRENGTH_VALIDITY_KEY = 'enforce.strength';

  constructor(private tenantUiService: TenantUiService) {}

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.authConfiguration && changes.authConfiguration.currentValue) {
      this.isOauth2 = !!changes.authConfiguration.currentValue.loginOptions.find(
        this.tenantUiService.isOauth2
      );
    }
  }

  get systemPasswordLimitValidity() {
    return this.authConfiguration.systemOptions[this.PASSWORD_CATEGORY][this.LIMIT_VALIDITY_KEY];
  }

  get passwordLimitValidity() {
    return this.systemPasswordLimitValidity !== null
      ? this.systemPasswordLimitValidity
      : this.authConfiguration.tenantOptions[this.PASSWORD_CATEGORY][this.LIMIT_VALIDITY_KEY];
  }

  set passwordLimitValidity(value) {
    if (this.systemPasswordLimitValidity === null) {
      this.authConfiguration.tenantOptions[this.PASSWORD_CATEGORY][this.LIMIT_VALIDITY_KEY] = value;
    }
  }

  get systemPasswordEnforceStrength() {
    return this.authConfiguration.systemOptions[this.PASSWORD_CATEGORY][
      this.SYSTEM_STRENGTH_VALIDITY_KEY
    ];
  }

  get passwordEnforceStrength() {
    return this.systemPasswordEnforceStrength
      ? this.systemPasswordEnforceStrength
      : this.authConfiguration.tenantOptions[this.PASSWORD_CATEGORY][
          this.TENANT_STRENGTH_VALIDITY_KEY
        ];
  }

  set passwordEnforceStrength(value) {
    if (!this.systemPasswordEnforceStrength) {
      this.authConfiguration.tenantOptions[this.PASSWORD_CATEGORY][
        this.TENANT_STRENGTH_VALIDITY_KEY
      ] = value;
    }
  }
}
