import { Component, EventEmitter, Input, Output } from '@angular/core';
import { ControlContainer, NgForm } from '@angular/forms';
import { pull } from 'lodash-es';
import { relations } from './dynamic-accesss-mapping-relations.model';
import { ChildPredicate } from '../../sso-configuration.model';

@Component({
  selector: 'c8y-sso-child-predicates',
  templateUrl: './child-predicates.component.html',
  viewProviders: [{ provide: ControlContainer, useExisting: NgForm }]
})
export class ChildPredicatesComponent {
  @Input() childPredicates: ChildPredicate[];
  @Input() accessMappingIndex: string;
  @Output() onRemoveAllChildPredicates: EventEmitter<null> = new EventEmitter();

  relations = relations;

  constructor(private controlContainer: ControlContainer) {}

  removeChildPredicate(childPredicate: ChildPredicate) {
    pull(this.childPredicates, childPredicate);
    if (this.childPredicates.length === 0) {
      this.onRemoveAllChildPredicates.emit();
    }
    this.controlContainer.control.markAsDirty();
  }

  addChildPredicate() {
    this.childPredicates.push({
      operator: 'EQ',
      parameterPath: '',
      value: ''
    });
  }
}
