import { Injectable } from '@angular/core';
import { CanActivate } from '@angular/router';
import { of, Observable } from 'rxjs';
import { catchError, mapTo } from 'rxjs/operators';
import { SsoConfigurationService } from './sso-configuration.service';

@Injectable()
export class SsoConfigurationGuard implements CanActivate {
  constructor(private ssoConfigurationService: SsoConfigurationService) {}

  canActivate(): Observable<boolean> {
    return this.ssoConfigurationService.getSsoConfiguration$().pipe(
      mapTo(true),
      catchError(() => of(false))
    );
  }
}
