import { Component, Output, Input, EventEmitter } from '@angular/core';
import { ControlContainer, NgForm } from '@angular/forms';
import { InventoryMapping, InventoryRole } from '../../sso-configuration.model';
import { BsModalService } from 'ngx-bootstrap/modal';
import { InventoryRolesModalComponent } from './inventory-roles-modal.component';
import { take } from 'rxjs/operators';
import { IIdentified } from '@c8y/client';

@Component({
  selector: 'c8y-sso-inventory-roles-mapping',
  templateUrl: './inventory-roles-mapping.component.html',
  viewProviders: [{ provide: ControlContainer, useExisting: NgForm }]
})
export class InventoryRolesMappingComponent {
  @Input() inventoryMapping: InventoryMapping;
  @Input() inventoryMappingIndex: number;

  @Input() inventoryRoles: IIdentified[] = [];
  selectedInventoryRoles: IIdentified[] = [];

  @Output() onRemoveInventoryMapping: EventEmitter<InventoryMapping> = new EventEmitter();

  constructor(private bsModal: BsModalService) {}

  ngOnChanges(): void {
    this.setSelectedInventoryRoles();
  }

  onRemoveAllChildPredicates() {
    this.onRemoveInventoryMapping.emit(this.inventoryMapping);
  }

  getIds(selectedItems: IIdentified[]) {
    return selectedItems.map(item => item.id);
  }

  removeInventoryRole(inventoryRole: InventoryRole) {
    this.inventoryMapping.thenInventoryRoles = this.inventoryMapping.thenInventoryRoles.filter(
      value => value.managedObject !== inventoryRole.managedObject
    );
    delete this.selectedInventoryRoles[inventoryRole.managedObject];
  }

  addInventoryRoles() {
    const currentlySelectedGroups: IIdentified[] = this.inventoryMapping.thenInventoryRoles.map(
      inventoryRole => ({ id: inventoryRole.managedObject })
    );
    const modal = this.bsModal.show(InventoryRolesModalComponent, {
      ignoreBackdropClick: true,
      class: 'modal-sm',
      ariaDescribedby: 'modal-body',
      ariaLabelledBy: 'modal-title',
      initialState: {
        selectedGroups: currentlySelectedGroups
      }
    });
    modal.content.resultEmitter.pipe(take(1)).subscribe(selectedGroups => {
      const newSelectedGroups = selectedGroups.filter(
        group => !currentlySelectedGroups.some(({ id }) => id === group.id)
      );
      const newInventoryRoles: InventoryRole[] = newSelectedGroups.map(
        group => ({ managedObject: group.id, roleIds: [] } as InventoryRole)
      );
      this.inventoryMapping.thenInventoryRoles =
        this.inventoryMapping.thenInventoryRoles.concat(newInventoryRoles);
    });
  }

  private setSelectedInventoryRoles() {
    if (this.inventoryMapping && this.inventoryMapping.thenInventoryRoles && this.inventoryRoles) {
      this.inventoryMapping.thenInventoryRoles.forEach(inventoryRole => {
        this.selectedInventoryRoles[inventoryRole.managedObject] = this.inventoryRoles.filter(
          item => inventoryRole.roleIds.includes(+item.id)
        );
      });
    }
  }
}
