import { Component, Input } from '@angular/core';
import { gettext } from '@c8y/ngx-components';
import { pull, defaultsDeep } from 'lodash-es';
import { IApplication, IIdentified, IUserGroup } from '@c8y/client';
import { ControlContainer, NgForm } from '@angular/forms';
import { InventoryMapping, AccessMapping } from '../../sso-configuration.model';

@Component({
  selector: 'c8y-sso-access-mapping',
  templateUrl: './access-mapping.component.html',
  viewProviders: [{ provide: ControlContainer, useExisting: NgForm }]
})
export class AccessMappingComponent {
  @Input() apps: IApplication[];
  @Input() groups: IUserGroup[];
  @Input() inventoryRoles: IIdentified[];
  @Input() templateModel;

  USE_ACCESS_MAPPING_ON_USER_CREATION_TOOLTIP = gettext(
    'The access mapping will be executed only once during the first login, then the administrator can edit the user roles. During the next login, these mappings will not be executed.'
  );
  USE_ACCESS_MAPPING_ON_USER_CREATION_OPTION = gettext(
    'Use dynamic access mapping only on user creation'
  );

  UPDATE_ROLES_LISTED_IN_ACCESS_MAPPING_ON_EACH_LOG_IN_TOOLTIP = gettext(
    'The access mapping will be executed only once during the first login, then the administrator can edit the user roles. During the next login only the roles listed in the access mappings will be updated.'
  );
  UPDATE_ROLES_LISTED_IN_ACCESS_MAPPING_ON_EACH_LOG_IN_OPTION = gettext(
    'Roles selected in the rules above will be reassigned to a user on each log in and other ones will be unchanged'
  );

  CLEAR_AND_UPDATED_ROLES_ON_EACH_LOG_IN_TOOLTIP = gettext(
    'The access mapping will be executed during the first login. The administrator cannot edit the SSO user roles. During the next login all the roles will be cleared and the mapping will be executed again.'
  );
  CLEAR_AND_UPDATED_ROLES_ON_EACH_LOG_IN_OPTION = gettext(
    'Roles selected in the rules above will be reassigned to a user on each log in and other ones will be cleared'
  );

  constructor(private controlContainer: ControlContainer) {}

  ngOnChanges(): void {
    if (this.templateModel && this.apps && this.groups) {
      this.setDynamicMapping();
    }
  }

  addAccessMapping() {
    const emptyMapping: AccessMapping = {
      when: {
        operator: 'AND',
        childPredicates: [{ parameterPath: '', operator: 'EQ', value: '' }]
      },
      thenGroups: [],
      thenApplications: []
    };
    this.templateModel.onNewUser.dynamicMapping.mappings.push(emptyMapping);
  }

  addInventoryMapping() {
    const emptyInventoryMapping: InventoryMapping = {
      when: {
        operator: 'AND',
        childPredicates: [{ parameterPath: '', operator: 'EQ', value: '' }]
      },
      thenInventoryRoles: []
    };
    this.templateModel.onNewUser.dynamicMapping.inventoryMappings.push(emptyInventoryMapping);
  }

  onRemove(accessMapping: AccessMapping) {
    pull(this.templateModel.onNewUser.dynamicMapping.mappings, accessMapping);
  }

  onRemoveInventoryMapping(inventoryMapping: InventoryMapping) {
    pull(this.templateModel.onNewUser.dynamicMapping.inventoryMappings, inventoryMapping);
  }

  setDynamicMappingConfiguration(
    mapRolesOnlyForNewUser: boolean,
    manageRolesOnlyFromAccessMapping = false
  ) {
    this.templateModel.onNewUser.dynamicMapping.configuration = {
      mapRolesOnlyForNewUser,
      manageRolesOnlyFromAccessMapping
    };
    this.controlContainer.control.markAsDirty();
  }

  get mapRolesOnlyForNewUser() {
    return this.templateModel.onNewUser.dynamicMapping.configuration.mapRolesOnlyForNewUser;
  }

  get manageRolesOnlyFromAccessMapping() {
    return this.templateModel.onNewUser.dynamicMapping.configuration
      .manageRolesOnlyFromAccessMapping;
  }

  private setDynamicMapping() {
    defaultsDeep(this.templateModel.onNewUser, {
      dynamicMapping: {
        mappings: [],
        inventoryMappings: [],
        configuration: { mapRolesOnlyForNewUser: false, manageRolesOnlyFromAccessMapping: false }
      }
    });
  }
}
