import { Component, Input } from '@angular/core';
import { gettext, memoize } from '@c8y/ngx-components';
import { ControlContainer, NgForm } from '@angular/forms';
import {
  algorithmTypeConfig,
  CertificateType,
  certificateTypeConfig,
  CustomCertificate
} from './signature-configuration.model';

@Component({
  selector: 'c8y-sso-signature-configuration',
  templateUrl: './signature-configuration.component.html',
  viewProviders: [{ provide: ControlContainer, useExisting: NgForm }]
})
export class SignatureConfigurationComponent {
  @Input() templateModel;

  certificateType = CertificateType;
  certificateTypes = certificateTypeConfig;

  algorithmTypes = algorithmTypeConfig;

  CERTIFICATE_ID_FIELD_POPOVER = gettext(
    'This is the name of the field in the token whose value will be used to select one of the certificates below which has matching "Certificate ID value".'
  );

  constructor(protected controlContainer: ControlContainer) {}

  @memoize()
  shouldShow(field: string) {
    return field in this.templateModel;
  }

  removeCustomCertificate(customCertificate: CustomCertificate) {
    this.templateModel.signatureVerificationConfig.manual.removeCustomCertificate(
      customCertificate
    );
    this.controlContainer.control.markAsDirty();
  }
}
