import { Input, Output, EventEmitter, Component, SimpleChanges } from '@angular/core';
import { SsoConfigurationMapper } from './sso-configuration.mapper';
import { SsoConfiguration } from '../sso-configuration.model';
import { Observable, Subscription } from 'rxjs';
import { IApplication, IIdentified, IUserGroup } from '@c8y/client';

@Component({
  template: ''
})
export abstract class TemplateComponent<T> {
  templateModel: T;
  @Input() apps: IApplication[];
  @Input() groups: IUserGroup[];
  @Input() inventoryRoles: IIdentified[];
  @Input() ssoConfiguration: SsoConfiguration;
  @Input() ssoConfigurationChangeTrigger: Observable<void>;
  @Output() ssoConfigurationChange = new EventEmitter<SsoConfiguration>();

  triggerSubscription: Subscription;

  protected constructor(protected configurationMapper: SsoConfigurationMapper<T>) {}

  ngOnInit() {
    this.triggerSubscription = this.ssoConfigurationChangeTrigger.subscribe(() => {
      this.emitSsoConfiguration();
    });
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.ssoConfiguration && changes.ssoConfiguration.currentValue) {
      this.mapSsoConfiguration(changes.ssoConfiguration.currentValue);
    }
  }

  ngOnDestroy() {
    this.triggerSubscription.unsubscribe();
  }

  private mapSsoConfiguration(ssoConfiguration) {
    this.templateModel = this.configurationMapper.mapTo(ssoConfiguration) as T;
  }

  private emitSsoConfiguration() {
    const ssoConfiguration = this.configurationMapper.mapFrom(this.templateModel);
    if (this.ssoConfiguration.id) {
      ssoConfiguration.id = this.ssoConfiguration.id;
    }
    this.ssoConfigurationChange.emit(ssoConfiguration);
  }
}
