import { Injectable } from '@angular/core';
import { gettext, NavigatorNode, NavigatorNodeFactory, Permissions } from '@c8y/ngx-components';

@Injectable()
export class NavigationFactory implements NavigatorNodeFactory {
  navs: NavigatorNode[] = [];

  constructor(private permissions: Permissions) {}

  async get() {
    const userHasPermission = this.permissions.hasAnyRole([
      'ROLE_TENANT_ADMIN',
      'ROLE_TENANT_MANAGEMENT_ADMIN'
    ]);
    if (this.navs.length === 0 && userHasPermission) {
      this.navs.push(
        new NavigatorNode({
          label: gettext('Authentication'),
          icon: 'c8y-shield',
          path: '/auth-configuration',
          parent: gettext('Settings'),
          routerLinkExact: false,
          priority: 2000
        })
      );
    }
    return this.navs;
  }
}
