import { Component, EventEmitter } from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { IIdentified } from '@c8y/client';
import { gettext } from '@c8y/ngx-components';

@Component({
  selector: 'c8y-sso-inventory-roles-modal',
  templateUrl: './inventory-roles-modal.component.html'
})
export class InventoryRolesModalComponent {
  selectedGroups: IIdentified[];

  resultEmitter: EventEmitter<IIdentified[]> = new EventEmitter<IIdentified[]>();

  label = gettext('Groups');

  constructor(public modal: BsModalRef) {}

  dismiss() {
    this.modal.hide();
  }

  select() {
    this.resultEmitter.emit(this.selectedGroups);
    this.modal.hide();
  }
}
