import { Component, Input, OnInit } from '@angular/core';
import { ControlContainer, NgForm } from '@angular/forms';
import { memoize } from '@c8y/ngx-components';

@Component({
  selector: 'c8y-user-id-configuration',
  templateUrl: './user-id-configuration.component.html',
  viewProviders: [{ provide: ControlContainer, useExisting: NgForm }]
})
export class UserIdConfigurationComponent implements OnInit {
  private static id = 0;

  @Input() userIdConfig;
  @Input() withHeader = true;

  componentId = 0;

  @memoize()
  shouldShow(field: string) {
    return field in this.userIdConfig;
  }

  ngOnInit(): void {
    this.componentId = ++UserIdConfigurationComponent.id;
  }
}
