import { Component, Input } from '@angular/core';
import { memoize } from '@c8y/ngx-components';
import { pull } from 'lodash-es';
import { ControlContainer, NgForm } from '@angular/forms';

@Component({
  selector: 'c8y-request-configuration',
  templateUrl: './request-configuration.component.html',
  viewProviders: [{ provide: ControlContainer, useExisting: NgForm }]
})
export class RequestConfigurationComponent {
  @Input() templateModel;
  @Input() requestName: string;
  @Input() requestType:
    | 'authorizationRequest'
    | 'tokenRequest'
    | 'refreshRequest'
    | 'logoutRequest'
    | 'tokenValidationRequest';

  constructor(private controlContainer: ControlContainer) {}

  @memoize()
  shouldShow(field: string) {
    return field in this.templateModel;
  }

  get requestConfiguration() {
    return this.templateModel[this.requestType];
  }

  addCustomValue(array) {
    const customValue = {
      key: '',
      value: ''
    };
    array.push(customValue);
  }

  removeCustomValue(array, customValue) {
    pull(array, customValue);
    this.controlContainer.control.markAsDirty();
  }

  get showBody() {
    return this.requestConfiguration.method === 'POST' && this.requestType !== 'logoutRequest';
  }
}
