import { Component, OnInit, EventEmitter } from '@angular/core';
import { AuthConfigurationService } from './auth-configuration.service';
import { AlertService, LoginService, ModalService } from '@c8y/ngx-components';
import { TenantLoginOptionsService } from '@c8y/client';
import { cloneDeep } from 'lodash-es';
import { BehaviorSubject, Observable, Subscription } from 'rxjs';
import { tap, switchMap, shareReplay } from 'rxjs/operators';
import { AuthConfiguration } from './auth-configuration.model';

@Component({
  selector: 'c8y-auth-configuration',
  templateUrl: './auth-configuration.component.html'
})
export class AuthConfigurationComponent implements OnInit {
  reloading$: BehaviorSubject<boolean> = new BehaviorSubject(false);
  reload: EventEmitter<void> = new EventEmitter();
  authConfiguration: AuthConfiguration;

  private authConfiguration$: Observable<AuthConfiguration> = this.reload.pipe(
    tap(() => this.reloading$.next(true)),
    switchMap(() => this.authConfigurationService.getAuthConfiguration$()),
    tap(() => this.reloading$.next(false)),
    shareReplay(1)
  );
  private previousAuthConfiguration: AuthConfiguration;
  private authConfigurationSubscription: Subscription;

  constructor(
    private authConfigurationService: AuthConfigurationService,
    private tenantLoginOptionsService: TenantLoginOptionsService,
    private modalService: ModalService,
    private loginService: LoginService,
    private alertService: AlertService
  ) {}

  ngOnInit() {
    this.authConfigurationSubscription = this.authConfiguration$.subscribe(
      (authConfiguration: AuthConfiguration) => {
        this.authConfiguration = authConfiguration;
        this.previousAuthConfiguration = cloneDeep(this.authConfiguration);
      }
    );
    this.loadAuthConfig();
  }

  loadAuthConfig() {
    this.reload.next();
  }

  ngOnDestroy(): void {
    this.authConfigurationSubscription.unsubscribe();
  }

  async save() {
    try {
      await this.modalService.confirmLogout();
      await this.authConfigurationService.save(
        this.authConfiguration,
        this.previousAuthConfiguration
      );
      await this.loginService.logout(true);
    } catch (ex) {
      if (ex) {
        this.alertService.addServerFailure(ex);
      }
    }
  }
}
