import { Component, Input, Output, EventEmitter } from '@angular/core';
import { ControlContainer, NgForm } from '@angular/forms';
import { IApplication, IUserGroup } from '@c8y/client';
import { AccessMapping } from '../../sso-configuration.model';

@Component({
  selector: 'c8y-sso-dynamic-access-mapping',
  templateUrl: './dynamic-access-mapping.component.html',
  viewProviders: [{ provide: ControlContainer, useExisting: NgForm }]
})
export class DynamicAccessMappingComponent {
  @Input() groups: IUserGroup[];
  selectedGroups: IUserGroup[];

  @Input() apps: IApplication[];
  selectedApps: IApplication[];

  @Input() accessMapping: AccessMapping;
  @Output() onRemoveAccessMapping: EventEmitter<AccessMapping> = new EventEmitter();

  @Input() accessMappingIndex: number;

  ngOnChanges(): void {
    if (this.accessMapping && this.apps && this.groups) {
      this.setSelectedItems();
    }
  }

  onRemoveAllChildPredicates() {
    this.onRemoveAccessMapping.emit(this.accessMapping);
  }

  getIds(selectedItems) {
    return selectedItems.map(item => parseInt(item.id, 10));
  }

  private setSelectedItems() {
    if (this.accessMapping) {
      if (this.accessMapping.thenGroups) {
        this.selectedGroups = this.groups.filter(item =>
          this.accessMapping.thenGroups.includes(item.id)
        );
      }
      if (this.accessMapping.thenApplications) {
        this.selectedApps = this.apps.filter(item =>
          this.accessMapping.thenApplications.includes(+item.id)
        );
      }
    }
  }
}
