import { Component, Input } from '@angular/core';
import { gettext } from '@c8y/ngx-components';
import { ControlContainer, NgForm } from '@angular/forms';

@Component({
  selector: 'c8y-sso-user-data-mapping',
  templateUrl: './user-data-mapping.component.html',
  viewProviders: [{ provide: ControlContainer, useExisting: NgForm }]
})
export class UserDataMappingComponent {
  @Input() templateModel;
  CLAIM_NAMES = gettext('Token claims can be checked in Audit Logs under Single sign-on type.');

  ngAfterContentInit() {
    if (!this.templateModel.accessTokenToUserDataMappings) {
      this.templateModel.accessTokenToUserDataMappings = {};
    }
  }
}
