import { Component, Input } from '@angular/core';
import { ControlContainer, NgForm } from '@angular/forms';
import { validationMethodConfig } from './external-token-config.model';
import { RequestConfiguration } from './request-configuration.model';

@Component({
  selector: 'c8y-external-token-config',
  templateUrl: './external-token-config.component.html',
  viewProviders: [{ provide: ControlContainer, useExisting: NgForm }]
})
export class ExternalTokenConfigComponent {
  @Input() templateModel;

  validationMethods = validationMethodConfig;

  get externalTokenConfig() {
    return this.templateModel.externalTokenConfig;
  }

  onValidationMethodChange(event) {
    this.externalTokenConfig.validationMethod = event;
    this.externalTokenConfig.tokenValidationRequest = new RequestConfiguration(
      this.validationMethods[event].defaults
    );
  }
}
