import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { gettext, Tab, TabFactory } from '@c8y/ngx-components';
import { SsoConfigurationService } from '../sso-configuration/sso-configuration.service';
import { catchError, mapTo, mergeAll, toArray } from 'rxjs/operators';
import { Observable, of, EMPTY, merge } from 'rxjs';

@Injectable()
export class TabsFactory implements TabFactory {
  constructor(public router: Router, private ssoConfigurationService: SsoConfigurationService) {}
  get(): Observable<Tab[]> {
    if (!this.router.url.match(/auth-configuration/g)) {
      return of([]);
    }

    const basicSettingsTab$ = of({
      path: 'auth-configuration/basic_settings',
      label: gettext('Basic settings'),
      icon: 'unlock-alt',
      priority: 1100,
      orientation: 'horizontal'
    } as Tab);
    const ssoTab$ = this.ssoConfigurationService.getSsoConfiguration$().pipe(
      mapTo({
        path: 'auth-configuration/single_sign-on',
        label: gettext('Single sign-on'),
        icon: 'sign-in',
        priority: 1050,
        orientation: 'horizontal'
      } as Tab),
      catchError(() => EMPTY)
    );
    return merge([basicSettingsTab$, ssoTab$]).pipe(mergeAll(), toArray());
  }
}
