import { Injectable } from '@angular/core';
import {
  GrantType,
  ITenantLoginOption,
  TenantLoginOptionsService,
  TenantLoginOptionType,
  UserManagementSource
} from '@c8y/client';
import { SsoConfiguration, TemplateType } from './sso-configuration.model';
import { defer, Observable, of, throwError } from 'rxjs';
import { catchError, map, publishReplay, refCount, take } from 'rxjs/operators';
import { HttpStatusCode } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class SsoConfigurationService {
  private ssoConfiguration$: Observable<ITenantLoginOption> = defer(() =>
    this.loginOptionsService.detail(TenantLoginOptionType.OAUTH2)
  ).pipe(
    map(res => res.data),
    catchError(({ res }) => {
      if (res.status === HttpStatusCode.Forbidden) return throwError(new Error());
      else return of(this.defaultConfiguration);
    }),
    publishReplay(1, 1000),
    refCount(),
    take(1)
  );

  private defaultConfiguration: SsoConfiguration = {
    authorizationRequest: {
      body: '',
      headers: {},
      method: 'GET',
      operation: 'REDIRECT',
      requestParams: {
        response_type: 'code',
        client_id: '${clientId}',
        redirect_uri: '${redirectUri}',
        scope: ''
      }
    },
    tokenRequest: {
      body: 'grant_type=authorization_code&code=${code}&redirect_uri=${redirectUri}&client_id=${clientId}',
      headers: {},
      method: 'POST',
      operation: 'EXECUTE',
      requestParams: {
        response_type: 'code',
        client_id: '${clientId}',
        redirect_uri: '${redirectUri}',
        scope: ''
      }
    },
    refreshRequest: {
      body: 'grant_type=refresh_token&refresh_token=${refreshToken}&client_id=${clientId}',
      headers: {
        Authorization: ''
      },
      method: 'POST',
      operation: 'EXECUTE',
      requestParams: {
        response_type: 'refresh',
        client_id: '${clientId}'
      }
    },
    logoutRequest: {
      headers: {},
      method: 'POST',
      operation: 'REDIRECT',
      requestParams: {}
    },
    type: TenantLoginOptionType.OAUTH2,
    grantType: GrantType.AUTHORIZATION_CODE,
    userManagementSource: UserManagementSource.REMOTE,
    onNewUser: {
      dynamicMapping: {
        mappings: [],
        inventoryMappings: [],
        configuration: {
          mapRolesOnlyForNewUser: false,
          manageRolesOnlyFromAccessMapping: false
        }
      }
    },
    userIdConfig: {
      jwtField: '',
      useConstantValue: true
    },
    signatureVerificationConfig: {
      manual: {
        certIdFromField: false
      }
    },
    template: TemplateType.CUSTOM,
    clientId: '',
    audience: '',
    issuer: '',
    buttonName: '',
    redirectToPlatform: '',
    providerName: '',
    visibleOnLoginPage: true,
    accessTokenToUserDataMappings: {},
    externalTokenConfig: {
      enabled: false
    }
  };

  constructor(private loginOptionsService: TenantLoginOptionsService) {}

  getSsoConfiguration$(): Observable<ITenantLoginOption> {
    return this.ssoConfiguration$;
  }

  save(ssoConfiguration: SsoConfiguration) {
    return this.loginOptionsService.save(ssoConfiguration);
  }
}
