import { Injectable } from '@angular/core';
import {
  GrantType,
  IResult,
  ITenantLoginOption,
  ITenantOption,
  SystemOptionsService,
  TenantLoginOptionsService,
  TenantLoginOptionType,
  TenantOptionsService,
  UserManagementSource,
  TenantService
} from '@c8y/client';
import { catchError, map } from 'rxjs/operators';
import { forkJoin, from, Observable, of } from 'rxjs';
import { defaults } from 'lodash-es';
import { AuthConfiguration, Options } from './auth-configuration.model';
import { AppStateService, TenantUiService } from '@c8y/ngx-components';
import { TypedOption } from './typed-option';
import { TenantLoginOption } from './basic-settings.model';
import { TenantLoginOptionMapper } from './tenant-login-option.mapper';

@Injectable()
export class AuthConfigurationService {
  private systemOptionsWithDefaultValue: TypedOption[] = [
    new TypedOption('password', 'limit.validity', 'number', null),
    new TypedOption('password', 'enforce.strength', 'boolean', false),
    new TypedOption('two-factor-authentication', 'tenant-scope-settings.enabled', 'boolean', false),
    new TypedOption('two-factor-authentication', 'enabled', 'boolean', false),
    // note: this definition is inconsistent with backend and is overridden in getSystemOptions$
    new TypedOption('two-factor-authentication', 'enforced', 'boolean', false),
    new TypedOption('two-factor-authentication', 'enforced.group', 'string', '')
  ];

  private tenantOptionsWithDefaultValue: TypedOption[] = [
    new TypedOption('password', 'limit.validity', 'number', 0),
    new TypedOption('password', 'strength.validity', 'boolean', false),
    new TypedOption('two-factor-authentication', 'enabled', 'boolean', false),
    new TypedOption('two-factor-authentication', 'token.validity', 'number', 43200), // 30 days
    new TypedOption('two-factor-authentication', 'pin.validity', 'number', 30),
    new TypedOption('two-factor-authentication', 'enforced', 'boolean', false),
    new TypedOption('two-factor-authentication', 'strategy', 'string', 'SMS'),
    new TypedOption('oauth.internal', 'basic-token.lifespan.seconds', 'number', null)
  ];

  constructor(
    private tenantLoginOptionsService: TenantLoginOptionsService,
    private tenantOptionsService: TenantOptionsService,
    private systemOptionsService: SystemOptionsService,
    private appState: AppStateService,
    private tenantUiService: TenantUiService,
    private tenantLoginOptionMapper: TenantLoginOptionMapper,
    private tenantService: TenantService
  ) {}

  getAuthConfiguration$(): Observable<AuthConfiguration> {
    const loginOptions$ = this.getLoginOptions$();
    return forkJoin({
      loginOptions: this.map(loginOptions$),
      tenantOptions: this.getTenantOptions$(),
      systemOptions: this.getSystemOptions$(),
      smsGatewayAvailable: this.isSmsApplicationAvailable$(),
      preferredLoginOptionType: this.getPreferredLoginOptionType$(loginOptions$)
    });
  }

  save(newAuthConfiguration: AuthConfiguration, previousAuthConfiguration: AuthConfiguration) {
    const tenantOptions = this.prepareTenantOptions(
      newAuthConfiguration,
      previousAuthConfiguration
    );
    const updateTenantOptions = tenantOptions.map(tenantOption =>
      this.tenantOptionsService.create(tenantOption)
    );
    const basicLoginOption = this.prepareBasicLoginOption(
      newAuthConfiguration,
      previousAuthConfiguration
    );
    const oauthInternalLoginOption = this.prepareOauthInternalLoginOption(
      newAuthConfiguration,
      previousAuthConfiguration
    );

    return Promise.all([
      this.saveOrUpdateLoginOption(basicLoginOption),
      this.saveOrUpdateLoginOption(oauthInternalLoginOption),
      updateTenantOptions
    ]);
  }

  private map(loginOptions$: Observable<ITenantLoginOption[]>): Observable<TenantLoginOption[]> {
    return loginOptions$.pipe(
      map(loginOptions =>
        loginOptions.map(loginOption => this.tenantLoginOptionMapper.mapTo(loginOption))
      )
    );
  }

  private saveOrUpdateLoginOption(
    loginOption: ITenantLoginOption
  ): Promise<IResult<ITenantLoginOption>> {
    return loginOption.id
      ? this.tenantLoginOptionsService.update(loginOption)
      : this.tenantLoginOptionsService.create(loginOption);
  }

  private prepareBasicLoginOption(
    newAuthConfiguration: AuthConfiguration,
    previousAuthConfiguration: AuthConfiguration
  ): ITenantLoginOption {
    const basicLoginOption = this.originalLoginOptionWithDefaults(
      previousAuthConfiguration,
      TenantLoginOptionType.BASIC
    );
    basicLoginOption.visibleOnLoginPage = this.visibleOnLoginPage(
      newAuthConfiguration,
      TenantLoginOptionType.BASIC
    );
    return this.tenantLoginOptionMapper.mapFrom(
      basicLoginOption,
      this.getLoginOptionFromAuthConfiguration(newAuthConfiguration, TenantLoginOptionType.BASIC)
    );
  }

  private prepareOauthInternalLoginOption(
    newAuthConfiguration: AuthConfiguration,
    previousAuthConfiguration: AuthConfiguration
  ): ITenantLoginOption {
    const oauthInternalLoginOption = this.originalLoginOptionWithDefaults(
      previousAuthConfiguration,
      TenantLoginOptionType.OAUTH2_INTERNAL
    );
    oauthInternalLoginOption.visibleOnLoginPage = this.visibleOnLoginPage(
      newAuthConfiguration,
      TenantLoginOptionType.OAUTH2_INTERNAL
    );
    return this.tenantLoginOptionMapper.mapFrom(
      oauthInternalLoginOption,
      this.getLoginOptionFromAuthConfiguration(
        newAuthConfiguration,
        TenantLoginOptionType.OAUTH2_INTERNAL
      )
    );
  }

  private originalLoginOptionWithDefaults(
    previousAuthConfiguration: AuthConfiguration,
    loginOptionType: TenantLoginOptionType
  ): TenantLoginOption {
    return defaults(
      {},
      this.getLoginOptionFromAuthConfiguration(previousAuthConfiguration, loginOptionType),
      this.getDefaultLoginOption(loginOptionType)
    );
  }

  private getLoginOptionFromAuthConfiguration(
    authConfiguration: AuthConfiguration,
    loginOptionType: TenantLoginOptionType
  ) {
    return authConfiguration.loginOptions.find(loginOption => loginOption.type === loginOptionType);
  }

  private visibleOnLoginPage(
    authConfiguration: AuthConfiguration,
    loginOptionType: TenantLoginOptionType
  ): boolean {
    return authConfiguration.preferredLoginOptionType === loginOptionType;
  }

  private prepareTenantOptions(
    newAuthConfiguration: AuthConfiguration,
    previousAuthConfiguration: AuthConfiguration
  ): ITenantOption[] {
    const getValue = (authCfg, tenantOption) =>
      authCfg.tenantOptions[tenantOption.category][tenantOption.key];
    const hasChanged = tenantOption =>
      getValue(newAuthConfiguration, tenantOption) !==
      getValue(previousAuthConfiguration, tenantOption);

    return this.tenantOptionsWithDefaultValue
      .filter(tenantOption => getValue(newAuthConfiguration, tenantOption) !== null)
      .filter(tenantOption => hasChanged(tenantOption))
      .map(tenantOption => ({
        category: tenantOption.category,
        key: tenantOption.key,
        value: getValue(newAuthConfiguration, tenantOption).toString()
      }));
  }

  private getLoginOptions$(): Observable<ITenantLoginOption[]> {
    return from(this.tenantLoginOptionsService.listForCurrentTenant()).pipe(
      map(res => res.data),
      map(loginOptions => this.addDefaultLoginOptions(loginOptions))
    );
  }

  private getPreferredLoginOptionType$(
    loginOptions$: Observable<ITenantLoginOption[]>
  ): Observable<TenantLoginOptionType> {
    return loginOptions$.pipe(
      map(loginOptions => {
        return this.tenantUiService.getPreferredLoginOption(loginOptions).type;
      })
    );
  }

  private addDefaultLoginOptions(loginOptions: ITenantLoginOption[]) {
    if (!loginOptions.find(this.tenantUiService.isBasic)) {
      loginOptions.push(this.getDefaultLoginOption(TenantLoginOptionType.BASIC));
    }
    if (!loginOptions.find(this.tenantUiService.isOauthInternal)) {
      loginOptions.push(this.getDefaultLoginOption(TenantLoginOptionType.OAUTH2_INTERNAL));
    }
    return loginOptions;
  }

  private getTenantOptions$(): Observable<Options> {
    return forkJoin(
      this.tenantOptionsWithDefaultValue.map((option: TypedOption) =>
        from(this.tenantOptionsService.detail(option)).pipe(
          map(res => {
            option.apply(res.data);
            return option;
          }),
          catchError(() => of(option))
        )
      )
    ).pipe(map(options => this.getOptionsObject(options)));
  }

  private getSystemOptions$(): Observable<Options> {
    return forkJoin(
      this.systemOptionsWithDefaultValue.map((option: TypedOption) => {
        const fixedOption = this.fixTfaEnforcedSystemOption(option);
        if (fixedOption) {
          return fixedOption;
        }

        return from(this.systemOptionsService.detail(option)).pipe(
          map(res => {
            option.apply(res.data);
            return option;
          }),
          catchError(() => of(option))
        );
      })
    ).pipe(map(options => this.getOptionsObject(options)));
  }

  /**
   * Returns an observable with fixed `two-factor-authentication.enforced` system option or null.
   * This method fixes problem with inconsistent value. System option `two-factor-authentication.enforced` is list of tenants when UI using boolean value.
   * This part will be removed after implementing new endpoint in MTM-50490.
   */
  private fixTfaEnforcedSystemOption(option: TypedOption): Observable<TypedOption> {
    if (option.category === 'two-factor-authentication' && option.key === 'enforced') {
      return from(this.tenantService.getTfaSettings(this.tenantUiService.currentTenant)).pipe(
        map(tfaSettings => {
          option.value = tfaSettings.enforcedOnSystemLevel.toString();
          return option;
        })
      );
    }
    return null;
  }

  private isSmsApplicationAvailable$(): Observable<boolean> {
    return from(this.appState.isApplicationAvailable('sms-gateway'));
  }

  private getOptionsObject(options: TypedOption[]) {
    return options.reduce((optionsObject, option) => {
      optionsObject[option.category] = optionsObject[option.category] || {};
      optionsObject[option.category][option.key] = option.getValue();
      return optionsObject;
    }, {});
  }

  private getDefaultLoginOption(tenantLoginOptionType: TenantLoginOptionType): ITenantLoginOption {
    return {
      userManagementSource: UserManagementSource.INTERNAL,
      grantType: GrantType.PASSWORD,
      providerName: 'Cumulocity',
      visibleOnLoginPage: false,
      type: tenantLoginOptionType
    };
  }
}
