import { Injectable } from '@angular/core';
import {
  UserAgent,
  AuthenticationRestrictions,
  TenantLoginOption,
  isBasic,
  isOauthInternal
} from './basic-settings.model';
import { ITenantLoginOption, IAuthenticationRestrictions } from '@c8y/client';
import { defaults, isEmpty, omitBy, omit } from 'lodash-es';

@Injectable({
  providedIn: 'root'
})
export class TenantLoginOptionMapper {
  mapTo(tenantLoginOption: ITenantLoginOption): TenantLoginOption {
    const loginOption: TenantLoginOption = omit(
      this.prapareTenantLoginOption(tenantLoginOption),
      'authenticationRestrictions'
    );
    if (isBasic(loginOption)) {
      loginOption.authenticationRestrictions = this.mapAuthenticationRestrictionsTo(
        tenantLoginOption.authenticationRestrictions
      );
    }
    return loginOption;
  }

  mapFrom(
    originalLoginOption: TenantLoginOption,
    newLoginOption: TenantLoginOption
  ): ITenantLoginOption {
    if (isBasic(originalLoginOption)) {
      return this.mapBasicLoginOption(originalLoginOption, newLoginOption);
    }
    if (isOauthInternal(originalLoginOption)) {
      return this.mapOauthInternalLoginOption(originalLoginOption, newLoginOption);
    }
    throw new Error(
      `TenantLoginOptionMapper: The tenant login option cannot be mapped. Login option with type: ${originalLoginOption.type} is not supported.`
    );
  }

  private mapAuthenticationRestrictionsTo(
    authenticationRestrictions: IAuthenticationRestrictions
  ): AuthenticationRestrictions {
    const restrictions = defaults({}, omitBy(authenticationRestrictions, isEmpty), {
      forbiddenUserAgents: [''],
      trustedUserAgents: [''],
      forbiddenClients: []
    });
    restrictions.forbiddenUserAgents = restrictions.forbiddenUserAgents.map(
      val => new UserAgent(val)
    );
    restrictions.trustedUserAgents = restrictions.trustedUserAgents.map(val => new UserAgent(val));
    return restrictions;
  }

  private mapBasicLoginOption(
    originalLoginOption: TenantLoginOption,
    newLoginOption: TenantLoginOption
  ) {
    const loginOption = omit(originalLoginOption, ['sessionConfiguration']);
    loginOption.authenticationRestrictions = this.mapAuthenticationRestrictionsFrom(
      newLoginOption.authenticationRestrictions
    );
    return loginOption;
  }

  private mapOauthInternalLoginOption(
    originalLoginOption: TenantLoginOption,
    newLoginOption: TenantLoginOption
  ) {
    const loginOption = omit(originalLoginOption, ['authenticationRestrictions']);
    newLoginOption.sessionConfiguration !== null
      ? (loginOption.sessionConfiguration = newLoginOption.sessionConfiguration)
      : delete loginOption.sessionConfiguration;
    return loginOption;
  }

  private mapAuthenticationRestrictionsFrom(
    authenticationRestrictions: AuthenticationRestrictions
  ): IAuthenticationRestrictions {
    return {
      trustedUserAgents: authenticationRestrictions.trustedUserAgents
        .filter(({ value }) => value)
        .map(({ value }) => value),
      forbiddenUserAgents: authenticationRestrictions.forbiddenUserAgents
        .filter(({ value }) => value)
        .map(({ value }) => value),
      forbiddenClients: authenticationRestrictions.forbiddenClients.filter(value => value)
    };
  }

  private prapareTenantLoginOption(tenantLoginOption: ITenantLoginOption): TenantLoginOption {
    return omit(tenantLoginOption, [
      'self',
      'strengthValidity',
      'tfaStrategy',
      'greenMinLength',
      'enforceStrength',
      'strengthValidity',
      '_type'
    ]);
  }
}
