import { ISystemOption, ITenantOption } from '@c8y/client';
import { isString } from 'lodash-es';

export class TypedOption implements ISystemOption, ITenantOption {
  constructor(
    public category: string,
    public key: string,
    public type: 'string' | 'boolean' | 'number',
    public defaultValue?: string | boolean | number,
    public value?: string
  ) {}

  apply(option: ISystemOption | ITenantOption) {
    this.category = option.category;
    this.key = option.key;
    this.value = option.value;
  }

  getValue() {
    try {
      return this.getValueByType();
    } catch (ex) {
      return this.defaultValue;
    }
  }

  private getValueByType() {
    switch (this.type) {
      case 'boolean':
        return this.value.toLowerCase() === 'true';
      case 'number':
        return this.parseNumberValue(this.value);
      case 'string':
        return this.parseStringValue(this.value);
      default:
        throw new TypeError();
    }
  }

  private parseNumberValue(stringValue: string) {
    const value = Number.parseInt(stringValue, 10);
    if (typeof value !== 'number' || isNaN(value)) {
      throw new Error();
    }
    return value;
  }

  private parseStringValue(value: string) {
    if (!isString(value)) {
      throw new Error();
    }
    return value;
  }
}
