import { Component, EventEmitter, OnInit } from '@angular/core';
import {
  ApplicationService,
  UserGroupService,
  IApplication,
  IUserGroup,
  TenantLoginOptionType,
  InventoryRoleService,
  IIdentified
} from '@c8y/client';
import {
  gettext,
  AlertService,
  ModalService,
  Status,
  LoginService,
  AppStateService,
  TenantUiService
} from '@c8y/ngx-components';
import { BehaviorSubject, forkJoin, Subscription, Subject } from 'rxjs';
import { shareReplay, tap, switchMap } from 'rxjs/operators';
import { SsoConfiguration, TemplateType, templateTypeConfig } from './sso-configuration.model';
import { SsoConfigurationService } from './sso-configuration.service';

@Component({
  selector: 'c8y-sso-configuration',
  templateUrl: './sso-configuration.component.html'
})
export class SsoConfigurationComponent implements OnInit {
  apps: IApplication[];
  groups: IUserGroup[];
  inventoryRoles: IIdentified[];
  ssoConfiguration: SsoConfiguration;

  templateType = TemplateType;
  templateTypeConfig = templateTypeConfig;

  reloading$: BehaviorSubject<boolean> = new BehaviorSubject(false);
  reload: EventEmitter<void> = new EventEmitter();
  saveSubject: Subject<void> = new Subject();

  private dataSubscription: Subscription;

  private data$: any = this.reload.pipe(
    tap(() => this.reloading$.next(true)),
    switchMap(() =>
      forkJoin({
        ssoConfiguration: this.ssoConfigurationService.getSsoConfiguration$(),
        apps: this.getApplications(),
        groups: this.getGroups(),
        inventoryRoles: this.getInventoryRoles()
      })
    ),
    tap(() => this.reloading$.next(false)),
    shareReplay(1)
  );

  constructor(
    private ssoConfigurationService: SsoConfigurationService,
    private applicationService: ApplicationService,
    private userGroupService: UserGroupService,
    private inventoryRoleService: InventoryRoleService,
    private alertService: AlertService,
    private modalService: ModalService,
    private loginService: LoginService,
    private appStateService: AppStateService,
    private tenantUiService: TenantUiService
  ) {}

  ngOnInit(): void {
    this.dataSubscription = this.data$.subscribe(
      ({ ssoConfiguration, apps, groups, inventoryRoles }) => {
        this.apps = apps;
        this.groups = groups;
        this.inventoryRoles = inventoryRoles;
        this.ssoConfiguration = ssoConfiguration;
      }
    );
    this.loadSsoConfiguration();
  }

  ngOnDestroy(): void {
    this.dataSubscription.unsubscribe();
  }

  loadSsoConfiguration() {
    this.reload.next();
  }

  async save(ssoConfiguration) {
    try {
      await this.warnAboutForceUsersLogOut();
      const logoutRequired =
        this.tenantUiService.getCurrentUserLoginMode() !== TenantLoginOptionType.BASIC;
      if (logoutRequired) {
        await this.modalService.confirmLogout();
      }
      await this.ssoConfigurationService.save(ssoConfiguration);
      if (logoutRequired) {
        await this.loginService.logout(true);
      } else {
        this.loadSsoConfiguration();
        this.alertService.success(gettext('Configuration saved.'));
      }
    } catch (ex) {
      if (ex) {
        this.alertService.addServerFailure(ex);
      }
    }
  }

  private getApplications(): Promise<void | IApplication[]> {
    return this.applicationService.list({ pageSize: 100 }).then(
      res => res.data,
      error => this.alertService.addServerFailure(error)
    );
  }

  private getGroups(): Promise<void | IUserGroup[]> {
    return this.userGroupService.list({ pageSize: 100 }).then(
      res => res.data,
      error => this.alertService.addServerFailure(error)
    );
  }

  private getInventoryRoles(): Promise<void | IIdentified[]> {
    return this.inventoryRoleService.list({ pageSize: 100 }).then(
      res => res.data,
      error => this.alertService.addServerFailure(error)
    );
  }

  private async warnAboutForceUsersLogOut() {
    const modalLabels = { ok: gettext('Update and log out users'), cancel: gettext('Cancel') };
    const modalBody = gettext(
      'Updating SSO configuration will log out all users logged with "OAI-Secure" or "Single sign-on redirect". Do you want to proceed?'
    );
    return await this.modalService.confirm(
      gettext('Force users to log out'),
      modalBody,
      Status.WARNING,
      modalLabels
    );
  }
}
